.App {
    width: 100vw;
    height: 100vh;
}

.header {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20px;
}

.header h1 {
    overflow: hidden;
    border-right: .1em solid orange;
    white-space: nowrap;
    margin: 25px auto 0;
    width: 15.75em;
    animation:
            typing 4s steps(40, end),
            blink-caret .75s step-end infinite;
}

@keyframes typing {
    from { width: 0 }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
}

.headshot {
    width: 250px;
    height: 250px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
}
.headshot img {
    display: inline;
    width: 250px;
}

.links {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 15px;
}

.links img {
    margin: 10px;
}

@media (max-width: 700px) {
    .header h1{
        font-size: 16px;
        width: 16.50em;
    }

    .headshot {
        width: 150px;
        height: 150px;
    }
    .headshot img {
        width: 150px;
    }
}